import React, { useContext } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'
import SCButton from 'components/SCButton'

const Tips = props => {

  const prescreenContext = useContext(PrescreenContext)

  const render = () => {
    return (
      <div className={classNames(styles.tips, "frame", { [styles.invisible]: !prescreenContext.tips })}>
        <div className="title">
          <span className="text">Full screen animation</span>
        </div>
        <SCButton title="Start" action={() => { props.gotoNext() }} style={["main-button"]} />
      </div>
    )
  }

  return render()
}

export default Tips
