import React, { useContext } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'
import SCButton from 'components/SCButton'

const FirstEar = props => {

  const prescreenContext = useContext(PrescreenContext)

  const render = () => {
    const currentEar = prescreenContext.earIndex === 1 ? "Right Ear!" : "Left Ear!"

    return (
      <div className={classNames(styles.firstEar, "frame", { [styles.invisible]: !prescreenContext.firstEar })}>
        <div className="container">
          <span className="text">{currentEar}</span>
          <span className="text">Awesome!</span>
          <div className="first-button">1</div>
          <span className="text">Find a quiet room.</span>
          <span className="text">..........</span>
          <div className="second-button">2</div>
          <span className="text">Now weâ€™re going to play some beep tones to find the right level.</span>
          <span className="text">Tell us how many you hear.</span>
        </div>
        <SCButton title="Play" action={() => {
          window.scAudio.resume()
          props.gotoNext()
        }} style={["main-button"]} />
      </div>
    )
  }
  return render()
}

export default FirstEar
