import React from 'react'
import { Route, Redirect } from 'react-router-dom'
import NoiseDetect from 'views/AudioTest/NoiseDetect'
import AudioTest from 'views/AudioTest'
import PreScreen from 'views/PreScreen'
import LottieTest from 'views/LottieTest'

const Routes = () => {
  return (
    <>
      <Route exact path="/" component={() => <Redirect to="/noiseDetect" />} />
      <Route exact path="/noiseDetect" component={NoiseDetect} />
      <Route path="/audiotest" component={AudioTest} />
      <Route path="/prescreen" component={PreScreen} />
      <Route path="/lottietest" component={LottieTest} />
    </>
  )
}

export default Routes
