import React, { useContext } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'
import SCButton from 'components/SCButton'

const SecondEar = props => {

  const prescreenContext = useContext(PrescreenContext)

  const render = () => {
    const nextEar = prescreenContext.earIndex === -1 ? "Right Ear" : "Left Ear"

    return (
      <div className={classNames(styles.secondEar, "frame", { [styles.invisible]: !prescreenContext.secondEar })}>
        <div className="title">
          <span className="text">
            Great!  Let's do the <span className='yellow'>{nextEar}</span> now.
          </span>
        </div>
        <SCButton title="Play" action={() => { props.gotoNext(0 - prescreenContext.earIndex) }} style={["main-button"]} />
      </div>
    )
  }
  return render()
}

export default SecondEar
