import React, { useContext, useEffect } from 'react'
import styles from './index.module.scss'
import 'styles/animation.css'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'

const TonePlay = props => {

  const prescreenContext = useContext(PrescreenContext)
  const display = prescreenContext.tonePlay

  useEffect(() => {
    if (display) {
      setTimeout(() => {
        props.gotoNext()
      }, 5000)
    }
  }, [prescreenContext])

  const render = () => {
    return (
      <div className={classNames(styles.tonePlay, "frame", { [styles.invisible]: !display })}>
        <div className="content">
          <div className="progress-bar">
            <div className="progress"></div>
          </div>
          <span className="message">Okay, the tones are playing...</span>
        </div>
      </div>
    )
  }
  return render()
}

export default TonePlay
