import React, { useContext } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'

const ChooseEar = props => {

  const prescreenContext = useContext(PrescreenContext)

  const render = () => {
    return (
      <div className={classNames(styles.chooseEar, "frame", { [styles.invisible]: !prescreenContext.chooseEar })}>
        <div className="text-wrapper">
          <span className="text">Ok. Let's set a</span>
          <span className="text green">Pre-screen listening level</span>
          <span className="text dot">...........</span>
          <span className="text">Choose an <span className='yellow'>ear</span></span>
        </div>
        <div className="button-wrapper">
          <div className="left-button" onClick={() => props.gotoNext(-1)}>Left</div>
          <div className="right-button" onClick={() => props.gotoNext(1)}>Right</div>
        </div>
      </div>
    )
  }
  return render()
}

export default ChooseEar
