import React, { useReducer } from 'react'

const initialState = {
  flowIndex: 0,
  earIndex: -1, // -1:left,1:right
  tips: true
}

const reducer = (state, action) => {
  switch (action.type) {
    case 'SET_DATA':
      const flowIndex = action.payload.flowIndex || 0
      return {
        ...state,
        ...(action.payload || {}),
        tips: flowIndex === 0,
        chooseEar: flowIndex === 1,
        firstEar: flowIndex === 2,
        tonePlay: [3, 6, 9, 12].includes(flowIndex),
        howMany: [4, 7, 10, 13].includes(flowIndex),
        newTone: [5, 11].includes(flowIndex),
        secondEar: flowIndex === 8
      }
    default:
      throw new Error()
  }
}

export const PrescreenContext = React.createContext({})

export const PrescreenContextProvider = (Children) => {
  const Provider = props => {
    const [state, dispatch] = useReducer(reducer, initialState)

    return (
      <PrescreenContext.Provider value={{ ...state, dispatch }}>
        <Children {...props} />
      </PrescreenContext.Provider>
    )
  }
  return Provider
}
