import React, { useContext } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'
import SCButton from 'components/SCButton'

const NewTone = props => {

  const prescreenContext = useContext(PrescreenContext)

  const render = () => {
    return (
      <div className={classNames(styles.newTone, "frame", { [styles.invisible]: !prescreenContext.newTone })}>
        <div className="title">
          <span className="text">
            Please listen to a <span className='green'>new</span> set of tones
          </span>
        </div>
        <SCButton title="Play" action={() => { props.gotoNext() }} style={["main-button"]} />
      </div>
    )
  }
  return render()
}

export default NewTone
