import React, { useContext } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'

const HowMany = props => {

  const prescreenContext = useContext(PrescreenContext)

  const chooseNumber = (number) => {
    console.log('[LogHowMany]===', number, '===LOG_TEST_END')
    props.gotoNext()
  }

  const render = () => {
    return (
      <div className={classNames(styles.howMany, "frame", { [styles.invisible]: !prescreenContext.howMany })}>
        <span className="text title">How many tones did you hear?</span>
        <div className="button-wrapper">
          <div className="button" onClick={() => { chooseNumber(0) }}>0</div>
          <div className="button" onClick={() => { chooseNumber(1) }}>1</div>
          <div className="button" onClick={() => { chooseNumber(2) }}>2</div>
          <div className="button" onClick={() => { chooseNumber(3) }}>3</div>
        </div>
      </div>
    )
  }
  return render()
}

export default HowMany
