import React, { useContext, useEffect } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext, PrescreenContextProvider } from 'contexts/PrescreenContext'
import { resetAudio, stopAudio } from 'utils/webAudio'
import Tips from './Tips'
import ChooseEar from './ChooseEar'
import FirstEar from './FirstEar'
import TonePlay from './TonePlay'
import HowMany from './HowMany'
import NewTone from './NewTone'
import SecondEar from './SecondEar'

const oscillator = window.oscillator
var volume = 0
var gate = 0

const PreScreen = () => {

  const prescreenContext = useContext(PrescreenContext)
  const flowIndex = prescreenContext.flowIndex
  const earIndex = prescreenContext.earIndex
  const tonePlay = prescreenContext.tonePlay

  useEffect(() => {
    if (tonePlay) {
      setToneFlow()
    } else {
      stopTone()
      oscillator.frequency.value = 250
    }
  }, [tonePlay])

  const setToneFlow = () => {
    playTone(250)
    setTimeout(() => {
      stopTone()
      oscillator.frequency.value = 1000
    }, 1000)
    setTimeout(() => {
      playTone(1000)
    }, 2000)
    setTimeout(() => {
      stopTone()
      oscillator.frequency.value = 2000
    }, 3000)
    setTimeout(() => {
      playTone(2000)
    }, 4000)
  }

  const playTone = (frequency) => {
    if ([3, 9].includes(flowIndex)) {
      volume = 70
    } else if ([6, 12].includes(flowIndex)) {
      volume = 40
    }
    gate = 1
    resetAudio(frequency, earIndex, (volume - 96), gate)
  }

  const stopTone = () => {
    volume = 0
    gate = 0
    stopAudio()
  }

  const gotoNext = (ear = earIndex) => {
    prescreenContext.dispatch({
      type: "SET_DATA",
      payload: {
        flowIndex: flowIndex + 1,
        earIndex: ear
      }
    })
  }

  const render = () => {
    const _props = { gotoNext }
    return (
      <div className={styles.preScreen}>
        <Tips {..._props} />
        <ChooseEar {..._props} />
        <FirstEar {..._props} />
        <TonePlay {..._props} />
        <HowMany {..._props} />
        <NewTone {..._props} />
        <SecondEar {..._props} />
        <div className={classNames("finished", { [styles.invisible]: flowIndex !== 14 })}>Finished PreScreen</div>
      </div>
    )
  }

  return render()
}

export default PrescreenContextProvider(PreScreen)