const resetDelay = 0.01

const scAudio = window.scAudio
const oscillator = window.oscillator
const gainNodeL = window.gainNodeL
const gainNodeR = window.gainNodeR

export const resetAudio = (frequency, ear, volume, gate, resetNow = false) => {
  scAudio.resume()
  oscillator.frequency.value = frequency
  const ampOutput = Math.pow(10, volume * 0.05) * gate
  const earInt = parseInt(ear)
  gainNodeL.gain.setTargetAtTime(ampOutput * (earInt !== 1 ? 1 : 0), 0, resetNow ? 0 : resetDelay)
  gainNodeR.gain.setTargetAtTime(ampOutput * (earInt !== -1 ? 1 : 0), 0, resetNow ? 0 : resetDelay)
}

export const stopAudio = (stopNow = false) => {
  scAudio.resume()
  gainNodeL.gain.setTargetAtTime(0, 0, stopNow ? 0 : resetDelay)
  gainNodeR.gain.setTargetAtTime(0, 0, stopNow ? 0 : resetDelay)
}

export const detectMediaDevices = () => {
  const constraints = { audio: true }
  navigator.mediaDevices.getUserMedia(constraints)
    .then(stream => {
      if (stream.active) {
        if (!navigator.mediaDevices || !navigator.mediaDevices.enumerateDevices) {
          console.log("[detectMediaDevices]", "enumerateDevices() not supported.")
          return
        }
        navigator.mediaDevices.enumerateDevices()
          .then(devices => {
            devices.forEach(device => {
              console.log("[detectMediaDevices]", device.kind + ": " + device.label + " id = " + device.deviceId + " group = " + device.groupId)
            })
          })
          .catch(function (err) {
            console.log("[detectMediaDevices]", err.name + ": " + err.message)
          })
      } else {
        console.log("[detectMediaDevices]", "mediaStream is not be granted")
      }
    })
    .catch(function (err) {
      console.log("[detectMediaDevices]", err)
    })
}


var noiseDetectData = {}

export const noiseDetect = (startDetect, stopDetect) => {
  if (noiseDetectData.stream) {
    noiseDetectData.stream.getTracks().forEach(track => track.stop())
    noiseDetectData.microphone.disconnect(noiseDetectData.analyser)
    noiseDetectData.analyser.disconnect(noiseDetectData.processor)
    noiseDetectData.processor.disconnect(noiseDetectData.noisyAudio.destination)
    noiseDetectData.noisyAudio.suspend()
    noiseDetectData.noisyAudio.close().then(() => {
      noiseDetectData.microphone = null
      noiseDetectData.analyser = null
      noiseDetectData.processor.onaudioprocess = null
      noiseDetectData.processor = null
      noiseDetectData.noisyAudio = null
      noiseDetectData = {}
      stopDetect()
    })
  } else {
    navigator.mediaDevices.getUserMedia({ audio: true })
      .then(stream => {
        noiseDetectData.noisyAudio = new (window.AudioContext || window.webkitAudioContext)()
        noiseDetectData.noisyAudio.resume()
        noiseDetectData.stream = stream
        noiseDetectData.microphone = noiseDetectData.noisyAudio.createMediaStreamSource(stream)
        noiseDetectData.analyser = noiseDetectData.noisyAudio.createAnalyser()
        noiseDetectData.analyser.smoothingTimeConstant = 0.3
        noiseDetectData.analyser.fftSize = 1024
        noiseDetectData.processor = noiseDetectData.noisyAudio.createScriptProcessor(2048, 1, 1)
        noiseDetectData.microphone.connect(noiseDetectData.analyser)
        noiseDetectData.analyser.connect(noiseDetectData.processor)
        noiseDetectData.processor.connect(noiseDetectData.noisyAudio.destination)

        noiseDetectData.processor.onaudioprocess = function () {
          var array = new Uint8Array(noiseDetectData.analyser.frequencyBinCount)
          noiseDetectData.analyser.getByteFrequencyData(array)
          var values = 0
          var length = array.length
          for (var i = 0; i < length; i++) {
            values += (array[i])
          }
          var average = values / length
          const volume = Math.round(average)
          startDetect(volume)
        }
      })
      .catch(function (err) {
        console.log('[LogError]===', 'getInputVolume', err, '===LOG_ERROR_END')
      })
  }
}
